<template>
  <div>
    <div class="list-content">
      <el-button type="info" disabled class="list-pro"></el-button>
      <el-select v-model="value" placeholder="" @change="changeProduction()" >
        <el-option
          v-for="item in productionArr"
          :key="item"
          :label="item"
          :value="item" class="list-input"
          >
        </el-option>
      </el-select>
      <div class="choosed"> >{{value}}</div><!--  -->
     </div>  
     <!--  -->
      <div class="time-content">
      <div class="time-position">
          <div class="block" style="display:inline-block;"> 
          <el-date-picker
            v-model="value7"
            type="daterange"
            align="center"
            unlink-panels
            range-separator=""
            start-placeholder=""
            end-placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
            @change="timechange"
            :picker-options="pickerOptions2"
            >
          </el-date-picker>
        </div>  <!--  -->
    </div><!--  -->
    </div>
     <!-- Element -->
     <div class="charts">
      <div class="choose-more">
      <el-select v-model="evalue" placeholder="" @change="getquota">
      <el-option
        v-for="item in quota"
        :key="item"
        :label="item"
        :value="item" 
       >
      </el-option>
      </el-select> 
     </div>
      <div class="choose-hourday">
           <el-radio-group v-model="radio3" @change="timechange">
      <el-radio-button label=""></el-radio-button>
      <el-radio-button label=""></el-radio-button>
    </el-radio-group> </div>    
      <div id="indexChart" :style="{width:'1180px',height:'400px'}"></div>
     </div>
     <!-- Echart- -->
      <div class="busi-s">
          <div class="busi-choose">
                 <el-select v-model="busiselectedShow" multiple
                  filterable
                  @change="pcmbChange"
                  placeholder="">
                      <el-option-group
                        v-for="group in ssBusi"
                        :key="group.label"
                        :label="group.label">
                        <el-option
                          v-for="item in group.businessArr"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-option-group>
                    </el-select>
          <el-input v-model="inputsearch" placeholder="" class="inputsearch"></el-input>
          <button class="btn-search" @click="searching()"></button>
          <el-button class="page-ml" @click="prepage"></el-button>
          <el-button  type="primary">{{pageval}}</el-button>
          <el-button class="next-btn" @click="nextpage"></el-button>
          <!-- pre-next page -->
        </div>
        
      </div>
      <!--  -->
      <div class="table-rel"> 
        <div class="shame" ><img style="width:16px;height:20px;" src="@/assets/forword-down.png" alt=""></div>
        <el-table
        v-loading="loading"
          :data="tableData"
           ref="table"
            @selection-change="handleSelectionChange"
    @sort-change="sortChange"
    :default-sort = "{prop: 'count', order: 'descending'}"
          >
          <el-table-column
      type="selection"
      width="55"
      label="">
          </el-table-column>
          <el-table-column
            prop="business"
            label=""
            width="180"
            sortable="custom"
            class-name="title-position" 
            >
          </el-table-column>
          <el-table-column
            prop="action"
            label=""
            sortable="custom"
            width="120">
          </el-table-column>
          <el-table-column
            prop="detail"
            label=""
            width="160">
            <template slot-scope="scope">
              <p @click="handleDetail(scope.row)"><img class="historypng" src="@/assets/history.png"></p>
            </template>
          </el-table-column>
          <el-table-column
            prop="usercount"
            label=""
            sortable="custom">
          </el-table-column>
          <el-table-column
            prop="count"
            label=""
            sortable="custom">
          </el-table-column>
          <el-table-column
            prop="succ_count"
            label=""
            sortable="custom">
          </el-table-column>
          <el-table-column
            prop="fail_count"
            label=""
            sortable="custom">
            <template slot-scope="scope">
              <div v-if="scope.row.fail_count!=0">
                <el-button
                  size="mini"
                  @click="errDetail(scope.row)">
                  <p class="err-wrapper">{{scope.row.fail_count}}</p>
                </el-button>
                </div>
                <div v-else>
                  <el-button
                  size="mini"
                  disabled
                   >
                  <p class="err-wrapper">0</p>
                </el-button>
                </div>
            </template>
          </el-table-column>
          <el-table-column
            prop="suss_time_average"
            width="180"
            label="ms"
            sortable="custom">
          </el-table-column>
      </el-table>
  </div>
  <!-- Table List - -->
    <div class="pagetag-content">
    <div class="block block-position" >
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage4"
      :page-sizes="[10,20,40,100]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="sum">
    </el-pagination>
     </div>
     </div>
     <!--  -->
  </div> 
</template>
<script>
import echarts from "echarts";
import index from "vue";
import EventBus from "../../js/bus";
export default {
  data() {
    return {
      ctype: "", //nav
      ctypeValue: "0",
      value: "", //
      businessdefine: {}, //()
      tableSum: [], //SumTable()
      productionArr: [], //
      
      val:[],//Table
      cacheVal:[],//Table
      totalResults: "", //ByTime/ByTimeEveryDay 
      details:[],//ByTime/ByTimeEveryDay  details
      painterArr:[],//
      
      ssBusi: [
        {label: 'PC/MB',
          businessArr: ["PC","MB"]}, 
          {label: '',
          businessArr:[]}//businessArr:[],//
          ],
      busiselectedShow: [], //    

      daycount:1,
      daycountEnd:1,
      pickerOptions2: {
          disabledDate(time) {
            return time.getTime() > Date.now();
          },
         shortcuts: [
           {
            text: '',
            onClick:(picker)=>{
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * (this.daycount++));
              const end=start;
              picker.$emit('pick', [end, start]);
            }
          },
           {
            text: '',
            onClick:(picker)=> {
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24* (-2+this.daycount--));
              const end = start;
              picker.$emit('pick', [start, end]);
            }
            
          },
            {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 0);
              picker.$emit('pick', [start, end]);
            }
          },
           {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 1);
              end.setTime(end.getTime() - 3600 * 1000 * 24 * 1);
              picker.$emit('pick', [start, end]);
            }
          },{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
      }, //
      value7: [new Date(), new Date()], //() [0]: [1] //
      radio: "1",
      quota: ["", "", "", "", ""], //
      evalue: "", //

      
      acted: "", //
      inputsearch:"",//
      businessAsk:[],
      actionAsk:{},
      list: [],
      loading: true,
      tableData: [], //table
      noTagPageTableData:[],
      valPainter:[],//nextick

      row:{},//row
      sum: 0, //
      changeSize:10,
      pagedata: [], //
      currentRow: null,
      currentPage: "1",
      pageval: "1", //
      currentPage2: 5,
      currentPage3: 5,
      currentPage4: 1,
      radio3: "", //
      lastWeek: "",
      usercount: [],
      count: [],
      fail_count: [],
      succ_count: [],
      suss_time_average: [],
        //table
      myxaxis: {},
      arr:[],
      tmp: [],//series
      xtmp: {
        type: "category",
        boundaryGap: false,
        data: []
      },
      legend:{},//

      lastVal:[],
      getStorageTableCheck:[],
      isFirst:1,
      isSearching:true,
      isSort:true,
      isTime:true,
    };
  },

  components: {},

  computed: {},

  created(){
     
  },//createddatactypeValue
  
  mounted() {
    this.InitFirst();//
  },
  
  beforeDestroy(){

  },//vue

  watch: {
  
  },

  methods: {
    //------------------------------------------------
        InitFirst:function(){
        this.productionGetItem()//
        this.businessdefinePort();//
        },
        businessdefinePort: function() {
          this.$http
            .get(
              "http://report.ruiyunnet.com/report/report/businessdefine?id=" + 0
            )
            .then(
              response => {
                this.businessdefine = response.data;
                //
                for (var item in this.businessdefine) {
                  this.productionArr.push(item);
                }//
                this.changeProduction();
              },
              response => {
                console.log("error");
              }
            );  
        },
        changeProduction:function(){
             this.echartClear();
             this.ssBusi[1].businessArr =this.businessdefine[this.value]; //
             for(var i=0;i<this.ssBusi[1].businessArr.length;i++){
               if(this.ssBusi[1].businessArr==""){
                  this.ssBusi[1].businessArr.splice(i,1);
               }
             }//
             this.totalResults=[];
             this.details=[];
             this.painterArr=[];// 
             var paramsByTimeSum = {
                  statType: this.ctypeValue, //
                  product: this.value, //
                  startTime: this.value7[0],
                  endTime: this.value7[1]
                  };
            this.gettable(paramsByTimeSum);//table 
            this.prodctionSetItem();
        },

    // -------------------------------------------------
        gettable: function(paramsByTimeSum){
          this.tableData=[];
          this.$http
            .post(
              "http://report.ruiyunnet.com/report/report/getBusinessByTimeSum",
              paramsByTimeSum)
            .then(
              response => {
                this.tableData=[];//tabledata
                this.tableSum= response.data;
                var details = response.data["details"];
                var totalResult = response.data["totalResult"];
                totalResult.business="";
                totalResult.action="";
                details.unshift(totalResult);//Table:totalResultdetails
                try{
                      for (var i = 0; i < details.length; i++) {
                      var newsearch = {
                        business: details[i].business,
                        action: details[i].action,
                        detail:"",
                        usercount: details[i].usercount,
                        count: details[i].count,
                        succ_count: details[i].succ_count,
                        fail_count: details[i].fail_count,
                        suss_time_average: details[i].suss_time_average,
                      };
                      this.tableData.push(newsearch);
                    } //()
                }
                catch(err){
                    console.log("===gettable===")
                }
                this.pageTag(); //
              },
              response => {
                console.log("error");
              }
            );
        }, //ByTimeSumtable
        pageTag: function() {
          var Listlength = this.tableData.length;
          this.noTagPageTableData=this.tableData;//tabledata
          this.sum = Listlength; //
          var n=this.changeSize;
            var pageNum = Math.ceil(this.tableData.length / n);
            var pagedata = [];
            for (var k = 0; k < pageNum; k++) {
              pagedata[k] = [];
              for (var a = k * n; a < (k + 1) * n; a++) {
                pagedata[k].push(this.tableData[a]);
              }
            }
            this.pagedata = pagedata;
            var lastPageData=[];
                for (var a = (pageNum-1) * n; a < (pageNum-1) * n+(this.tableData.length % n); a++) {
                  lastPageData.push(this.tableData[a]);
                }//
            this.pagedata.splice(-1,1,lastPageData);
            this.handleCurrentChange(1); //
        }, //n
        handleCurrentChange(val) {
          this.totalResults=[];
          this.details=[];
          this.painterArr=[];//push
          this.pageval = val;
          var pageval = this.pageval - 1; //
          this.tableData = this.pagedata[pageval];//
          /*
          nexTickTableEchart 
          */
          this.$nextTick(function(){
            if(this.isSearching==true&this.isSort==true){
                  if(this.valPainter.length==0){
                    this.toggleSelection([this.noTagPageTableData[0],this.noTagPageTableData[1]]);
                  }else{
                      this.toggleSelection(this.valPainter);
                  } 
            }
            else{
              this.isSearching=true
              this.echartClear();
              this.toggleSelection([this.noTagPageTableData[0]]);//Echart
            }
          }
          ) 
          this.loading=false;
        }, //table

    // -------------------------------------------------
        toggleSelection(rows) {
          if (rows) {
            rows.forEach(row => {
              this.$refs.table.toggleRowSelection(row);
            });
          } else {
            this.$refs.table.clearSelection();
          }
        },//
        handleSelectionChange(val) {
          this.val=val;

          this.getVal();//this.valPainter
          try{
             var CurrentChecked=val[val.length-1];//
             this.businessAsk=CurrentChecked.business;
             this.actionAsk=CurrentChecked.action;
                if(this.businessAsk==""){
                  this.mysearchTotal();
                }else{ 
                  this.mysearchDetails();
                }
           }//mysearchTotal:mysearchDetails
          catch (err){
            console.log("===handleSelectionChange===");
          } 
        }, //()//;
        mysearchTotal:function(){
          var eparamTotal={
               statType: this.ctypeValue, //
               product: this.value, //
               startTime: this.value7[0],
               endTime: this.value7[1]
          }
         if (this.radio3 == "") {
            this.$http
              .post(
                "http://report.ruiyunnet.com/report/report/getBusinessByTime",
                eparamTotal
              )
              .then(
                response => {
                  this.totalResults = response.data.totalResults;
                  for(var i=0;i<this.totalResults.length;i++){
                      this.totalResults[i].business="";
                      this.totalResults[i].action="";
                    }//""business
                  this.getpainterArr();
                },
                response => {
                  console.log("error");
                }
              );
          } else {
            this.$http
              .post(
                "http://report.ruiyunnet.com/report/report/getBusinessByTimeEveryDay",
                eparamTotal
              )
              .then(
                response => {
                 this.totalResults = response.data.totalResults;
                  for(var i=0;i<this.totalResults.length;i++){
                      this.totalResults[i].business="";
                      this.totalResults[i].action="";
                    }//""business
                  this.getpainterArr();
                },
                response => {
                  console.log("error");
                }
              );
          }
        },//totaldetails
        mysearchDetails: function() {
          var eparam = {
                statType: this.ctypeValue, //
                product: this.value, //
                business:[this.businessAsk], //
                action:this.actionAsk,//
                startTime: this.value7[0],
                endTime: this.value7[1]
          };
          if (this.radio3 == "") {
            this.$http
              .post(
                "http://report.ruiyunnet.com/report/report/getBusinessByTime",
                eparam
              )
              .then(
                response => {
                   this.details=response.data.details;
                   this.getpainterArr();
                },
                response => {
                  console.log("error");
                }
              );
          } else {
            this.$http
              .post(
                "http://report.ruiyunnet.com/report/report/getBusinessByTimeEveryDay",
                eparam
              )
              .then(
                response => {
                  this.details=response.data.details;
                  this.getpainterArr();
                },
                response => {
                  console.log("error");
                }
              );
          }
        },//myserchByTimeByTimeEveryDay
          //,;
          
        getpainterArr:function(){
            if(this.totalResults.length!=0){
              this.painterArr.unshift(this.totalResults);
            } 
            if(this.details.length!=0){
              this.painterArr.push(this.details);
            }//push
          /*
          this.painterArr
          */
          var difference=[];//painterArrval
          var valNow=[];
          var painterArrNow=[];
          if(this.val.length!=this.painterArr.length){
                for(var i=0;i<this.val.length;i++){
                  valNow.push(this.val[i].business+"~"+this.val[i].action)
                }
                for(var i=0;i<this.painterArr.length;i++){
                  painterArrNow.push(this.painterArr[i][0].business+"~"+this.painterArr[i][0].action);
                }
            let a=valNow;
            let b=painterArrNow; 
            difference = a.concat(b).filter(v => !a.includes(v) || !b.includes(v));//
          }//()
          for(var k=0;k<difference.length;k++){
            for(var i=0;i<painterArrNow.length;i++){
                      if(painterArrNow[i]==difference[k]){
                             this.painterArr.splice(i,1)
                        }  }
          }
          this.painterArr=this.deleteRepeatArrobj(this.painterArr);//
          var arr=this.painterArr;
         //arrXX( )
          var usercount = [];
          var count = [];
          var fail_count = [];
          var succ_count = [];
          var suss_time_average = [];
          var myxaxis = [];
          for (var k = 0; k < arr.length; k++) {
            usercount[k] = [];
            count[k] = [];
            fail_count[k] = [];
            succ_count[k] = [];
            suss_time_average[k] = [];
            myxaxis[k] = [];
              for (var i = 0; i < arr[k].length; i++) {
                usercount[k].push(arr[k][i].usercount);
                count[k].push(arr[k][i].count);
                fail_count[k].push(arr[k][i].fail_count);
                succ_count[k].push(arr[k][i].succ_count);
                suss_time_average[k].push(arr[k][i].suss_time_average);
                myxaxis[k].push(arr[k][i].listdate);
              }
            }
          //
          this.usercount = usercount;
          this.count = count;
          this.fail_count = fail_count;
          this.succ_count = succ_count;
          this.suss_time_average = suss_time_average;
          for(var i=0;i<myxaxis.length;i++){
            if(myxaxis[i].length>0){
               this.myxaxis=this.trim(myxaxis[i]);
              break;
            }
          }//
          this.getquota();
        },
        getquota: function() {
          this.echartClear();
          var nowitem;
          if (this.evalue == "") {
            nowitem = this.usercount;
          } else if (this.evalue == "") {
            nowitem = this.count;
          } else if (this.evalue == "") {
            nowitem = this.succ_count;
          } else if (this.evalue == "") {
            nowitem = this.fail_count;
          } else if (this.evalue == "") {
            nowitem = this.suss_time_average;
          } //
          var mylegend={};   
          var myytmp=[];
          for (var k=0;k<nowitem.length;k++){
            myytmp[k]=[];
            myytmp[k]={
                name:  "NO." +
                (k + 1) +
                this.value +
                "-" +
                this.painterArr[k][0].business+
                  "-" +
                this.painterArr[k][0].action+
                "-" +
                this.evalue,
                type:'line',
                data:nowitem[k],
                markPoint: {
                    data: [
                        {type: 'max', name: ''},
                        {type: 'min', name: ''}
                    ]
                },
                markLine: {
                    data: [
                        {type: 'average', name: ''}
                    ]
                }
            }

          }
          var myxtmp ={
            type: 'category',
            boundaryGap: false,
            data: this.myxaxis
          }
          this.tmp =myytmp;
          this.xtmp =myxtmp;
          this.echart(); //
        }, //Echart,
        echart: function() {
          var indexChart = echarts.init(document.getElementById("indexChart"));
          indexChart.clear();
              indexChart.setOption({
                tooltip: {
                    trigger: 'axis'
                },
                legend:this.legend,
                toolbox: {
                    show: true,
                    feature: {
                        dataView: {readOnly: false},
                        magicType: {type: ['line', 'bar']},
                        restore: {},
                        saveAsImage: {}
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value}'
                    }
                },
                    grid: {
                      left: "10%",
                      right: "4%",
                      bottom: "3%",
                      containLabel: true
                    },
                    xAxis: this.xtmp,
                    series: this.tmp,
                  });
          // --------------------
              indexChart.showLoading({
                  text : '',
                  textStyle : {
                      fontSize : 20
                  }
              });
              
              setTimeout(function (){
                  indexChart.hideLoading();
              },2000);
        }, //echart loading
        echartClear:function(){
          this.tmp=[];
          this.xtmp={};
          this.echart();
        }, //Echart

    // --------------------------------------

      // ------
         mytime: function(day, x) {
            var day = new Date();
            day.setTime(day.getTime() - 24 * 60 * 60 * x);
            var s =
              day.getFullYear() +
              "-" +
              (day.getMonth() + 1) +
              "-" +
              day.getDate() +
              " " +
              "00:00:00";
            return s;
          }, //XX
          timechange: function() {
            this.isTime=false;
            var param = {
              statType: this.ctypeValue, //
              product: this.value, //
              startTime: this.value7[0],
              endTime: this.value7[1]
            };
            this.totalResults=[];
            this.details=[];
            this.painterArr=[];//push
            var valPainterHalf=[];
              for(var i=[(this.valPainter.length)/2]-1;i<this.valPainter.length;i++){
                valPainterHalf.push(this.valPainter[i]);
              }
            
            this.gettable(param);
          }, //
      //------PCMB-----
          pcmbChange:function(){
             var nowpcmb=[];
             var a=["PC","MB"];
            if(this.busiselectedShow=="PC"){
              for(var i=0;i<this.ssBusi[1].businessArr.length;i++){
                if(this.ssBusi[1].businessArr[i].indexOf("PC")!=-1){
                  nowpcmb.push(this.ssBusi[1].businessArr[i]);
                }
              } 
            }//PC
            else if(this.busiselectedShow=="MB"){
              for(var i=0;i<this.ssBusi[1].businessArr.length;i++){
                if(this.ssBusi[1].businessArr[i].indexOf("MB")!=-1){
                  nowpcmb.push(this.ssBusi[1].businessArr[i]);
                }
              }
            }//MB
            else if(this.busiselectedShow.sort().toString()==a.sort().toString()){
              for(var i=0;i<this.ssBusi[1].businessArr.length;i++){
                if(this.ssBusi[1].businessArr[i].indexOf("PC")!=-1){
                 nowpcmb.push(this.ssBusi[1].businessArr[i]);
                }
                if(this.ssBusi[1].businessArr[i].indexOf("MB")!=-1){
                  nowpcmb.push(this.ssBusi[1].businessArr[i]);
                }
              }
            }//MB&PC
          
            return nowpcmb;
          },//PC/MB;
      //------
          searching:function(){
                this.isSearching=false;
                if(this.pcmbChange().length!=0){
                   this.tableData=[];
                    var param={
                          statType: this.ctypeValue, //
                          product: this.value, //
                          business: this.pcmbChange(),
                          startTime: this.value7[0],
                          endTime: this.value7[1]}
                    this.gettable(param);
                }//PC/MB

                else{
                            var param={
                                statType: this.ctypeValue, //
                                product: this.value, //
                                business: this.busiselectedShow,
                                action:this.inputsearch,
                                startTime: this.value7[0],
                                endTime: this.value7[1]}
                            this.gettable(param);   
                    }
          },//:

      //---Size//---
          handleSizeChange(val) {
            this.changeSize=val;
            this.changeProduction();
          },//Size
          prepage() {
            if(this.pageval>1){
              var val=this.pageval;
              this.pageval = val-1;
              var pageval = this.pageval - 1; //
              var pagedata = this.pagedata;
              this.tableData = pagedata[pageval];
            }
          }, //
          nextpage() {
            if(this.pageval<Math.ceil(this.sum/10)){
            var val=this.pageval;
            this.pageval = val+1;
            var pageval = this.pageval - 1; //
            var pagedata = this.pagedata;
            this.tableData = pagedata[pageval];
            }
          }, //

      //------
          sortChange:function(prop){
           if(this.isFirst==1){
             this.isFirst++;
           }else{this.isSort=false;}

           if(prop.order=="descending"||prop.order=="ascending"){
              if(prop.order=="descending"){
                this.tableData=this.noTagPageTableData.sort(this.byasc(prop.prop)).reverse()
              }else{
                this.tableData=this.noTagPageTableData.sort(this.byasc(prop.prop))
              }
              this.pageTag();
            }
          },//tabletable,Element
          byasc: function(name){
                return function(o, p){
                  var a, b;
                  if (typeof o === "object" && typeof p === "object" && o && p) {
                    a = o[name];
                    b = p[name];
                    if (a === b) {
                      return 0;
                    }
                    if (typeof a === typeof b) {
                      return a < b ? -1 : 1;
                    }
                    return typeof a < typeof b ? -1 : 1;
                  }
                  else {
                    throw ("error");
                  }
                }//
          },//


      //----------------
          trim:function(arr){
            if(arr.length==0){
              return
            }
            var strArr=[]
            for(var i=0,len=arr.length;i<len;i++){
              var newstr=arr[i].substr(0,4)+'.'+arr[i].substr(4,2)+'.'+arr[i].substr(6,2)+' '+arr[i].substr(8,2)+':00'
              strArr.push(newstr)
            }
            return strArr
          },
      //------------
          deleteRepeatArrobj(arr){
            var StringData=[];
            var noRepeatArr=[];
            for(var i=0;i<arr.length;i++){
                StringData.push(JSON.stringify(arr[i]))
            }
            var StringData = Array.from(new Set(StringData));//
            for(var i=0;i<StringData.length;i++){
              noRepeatArr.push(JSON.parse(StringData[i]))
            }
            return noRepeatArr;
          },//arr
      //------
          handleDetail:function(row){
            this.row=row;
            var row=this.row;
            var param={
              statType: this.ctypeValue, //
                        product: this.value, //
                        business: row.business, //
                        action: row.action,
                        startTime: this.value7[0],
                        endTime: this.value7[1]
            };
            window.open("history.html"+"?param="+JSON.stringify(param));
          },//URLparam
          errDetail:function(row){
            this.row=row;
            var row=this.row;
            var param={
              statType: this.ctypeValue, //
                        product: this.value, //
                        business: row.business, //
                        action: row.action,
                        startTime: this.value7[0],
                        endTime: this.value7[1]
            };
            window.open("err.html"+"?param="+JSON.stringify(param));
          },//URLparam    
      //---localstorage---------
        prodctionSetItem:function(){
            localStorage.setItem("nav1",this.value);
        },
        productionGetItem:function(){
          this.value=localStorage.getItem("nav1");//this.value
          if(this.value==null){
            this.value=""
          } // 
        },
        tableCheckSetItem:function(){
          localStorage.setItem("table",JSON.stringify(this.val));
        },
        tableCheckGetItem:function(){
          this.cacheVal=JSON.parse(localStorage.getItem("table"));
        },
      //-----------------
       getVal:function(){
          for(var k=0;k<this.val.length;k++){
               for(var i=0;i<this.noTagPageTableData.length;i++){
                  if(this.noTagPageTableData[i].business==this.val[k].business&
                  this.noTagPageTableData[i].action==this.val[k].action){
                    this.valPainter.push(this.noTagPageTableData[i])
                   }//tableDatathis.valPainter
                }
            }
            this.valPainter=this.deleteRepeatArrobj(this.valPainter);//
      }
 }
};
