  $(".btn-search").hover(
    function() {
      $(this).css("background", "#4f7bb8");
    },
    function() {
      $(this).css("background", "#2e5eab");
    }
  );
  $(".btn-search").click(function() {
    $(this).css("background", "#1d4185");
  });
});//
</script>
